<template>
  <section class="hero is-light is-medium">
    <div class="hero-body">
      <div class="container">
        <div
          class="columns"
        >
          <div
            class="column"
          >
            {{ $t("message.hello") }}
            <p v-if="birthdate">
              {{ $t("message.day_of_birth") }} {{ formatDate(birthdate) }}
            </p>
            <p v-if="birthdate">
              {{ $t("message.your_days") }} - {{ $tc('message.days', lifedays) }}
            </p>
          </div>
        </div>
        <div class="columns">
          <div class="column">
            <BirthdateInput />
          </div>
        </div>
        <div class="columns">
          <div class="column" />
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  import BirthdateInput from '@/components/BirthdateInput.vue';
  import { mapActions, mapState } from 'vuex';
  import {DateTime} from "luxon";
  export default {
    name: 'Home',
    components: {
      BirthdateInput
    },
    computed: {
      ...mapState({
        birthdate: state => state.birthdate,
        lifedays: state => state.lifedays,
      })
    },
    methods: {
      formatDate (payload) {
        let value = `${payload}`;
        if (value.includes('GMT+')) {
          value = `${value.replace('GMT+', '+')}`;
        }

        if (value.includes('(') && value.includes(')')) {
          value = `${value.slice(0, value.indexOf('(')).trim()}`;
        }

        const bdt = DateTime.fromFormat(value, this.$t('message.date_mask'));

        return (bdt.isValid) ? bdt.toLocaleString() : `${payload}`;
      }
    }
  }
</script>
